.App {
    text-align: center;
    font-family: 'Lucida Grande', sans-serif;
    color: #333;
  }
  
  .game-area {
    position: relative;
    margin: 40px auto;
    background-color: #f0f0f0;
    border: 4px solid #333;
    box-shadow: 0 0 20px rgba(0,0,0,0.2);
  }
  
  .snake {
    position: absolute;
    width: 25px; 
    height: 25px; 
    background-color: #20C20E;
    transition: all 0.2s ease;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
  }
  
  .food {
    position: absolute;
    width: 25px;
    height: 25px; 
    background-color: #FF4136;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
  }
  
  button {
    background-color: #0074D9;
    color: white;
    border: none;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0053ba;
  }
  
  button:focus {
    outline: none;
  }
  
  .score {
    font-size: 20px;
    margin-bottom: 20px;
  }
  
  .overlay {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .screen {
    padding: 20px;
    background: #FFF;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    text-align: center;
  }
  
  .startScreen, .gameOverScreen {
    display: inline-block;
    margin: auto;
    padding: 2em;
    border-radius: 15px;
    transition: transform 0.3s ease;
  }
  
  .startScreen h2, .gameOverScreen h2 {
    color: #333;
    font-size: 2em;
  }
  
  .startScreen p, .gameOverScreen p {
    margin-top: 20px;
    color: #666;
  }
  
  .startScreen button, .gameOverScreen button {
    font-size: 1em;
    padding: 0.5em 2em;
    margin-top: 20px;
  }
  
  .startScreen:hover, .gameOverScreen:hover {
    transform: scale(1.05);
  }
  
  
  button.startButton, button.restartButton {
    background-color: #4CAF50; 
    color: white;
  }
  
  button.startButton:hover, button.restartButton:hover {
    background-color: #45a049;
  }